:host {
  display: block;
  .td-expansion-panel-header {
    position: relative;
    outline: none;
    &:focus:not(.mat-disabled),
    &:hover:not(.mat-disabled) {
      cursor: pointer;
    }
    .td-expansion-panel-header-content {
      height: 48px;
      padding: 0 16px;
      // layout
      box-sizing: border-box;
      display: flex;
      // layout row
      flex-direction: row;
      // flex
      flex: 1;
      // layout-align start center
      justify-content: start;
      align-items: center;
      align-content: center;
      max-width: 100%;
      .td-expansion-label {
        // flex
        flex: 1;
      }
    }
  }
}

.td-expansion-label,
.td-expansion-sublabel {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 5px;
  /deep/ [dir='rtl'] & { 
    margin-left: 5px;
    margin-right: inherit;
  }
}